import listItem from "./list_item"
export default {
	components: {
		"list-item": listItem
	},
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0
		};
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		}
	}
}
